.heartbeat {
  position: fixed;
  bottom: 30%;
  right: 10px;
  border-radius: 50%;
  border: 1px solid red;
  padding: 10px;
}

.heart {
  width: 100px;
}

.steady {
  -webkit-animation: heartbeat 1s infinite;
          animation: heartbeat 1s infinite;
}

@-webkit-keyframes heartbeat
{
  0%
  {
    -webkit-transform: scale( .75 );
            transform: scale( .75 );
  }
  20%
  {
    -webkit-transform: scale( 1 );
            transform: scale( 1 );
  }
  40%
  {
    -webkit-transform: scale( .75 );
            transform: scale( .75 );
  }
  60%
  {
    -webkit-transform: scale( 1 );
            transform: scale( 1 );
  }
  80%
  {
    -webkit-transform: scale( .75 );
            transform: scale( .75 );
  }
  100%
  {
    -webkit-transform: scale( .75 );
            transform: scale( .75 );
  }
}

@keyframes heartbeat
{
  0%
  {
    -webkit-transform: scale( .75 );
            transform: scale( .75 );
  }
  20%
  {
    -webkit-transform: scale( 1 );
            transform: scale( 1 );
  }
  40%
  {
    -webkit-transform: scale( .75 );
            transform: scale( .75 );
  }
  60%
  {
    -webkit-transform: scale( 1 );
            transform: scale( 1 );
  }
  80%
  {
    -webkit-transform: scale( .75 );
            transform: scale( .75 );
  }
  100%
  {
    -webkit-transform: scale( .75 );
            transform: scale( .75 );
  }
}


